import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Start from './Start';

ReactDOM.render(<Start />, document.getElementById('root'));
console.clear();
document.cookie.split(";").forEach((c) => {
document.cookie = c
    .replace(/^ +/, "")
    .replace(/=.*/, "=;expires=" + new Date().toUTCString() + ";path=/");
});