import '../style.css';
import ReactDOM from 'react-dom';
import Start from '../Start';
import firebase from 'firebase';
import React, { useState } from 'react';
import { sha256 } from 'js-sha256';
import c from './crypt.json';
import logo from '../logo.png';

var EmployeesRegUser;
var EmployeesReg;
var kodas;
var password;
var Data;
var check = 0;
export function RegCheck(key) {
  return(
    kodas = document.getElementById('code').value,
    password = document.getElementById('password').value,
    EmployeesRegUser = firebase.database().ref("User"),
    EmployeesRegUser.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
          if (Data.userid === kodas){
            check = 1;
          }
      });
      if(check === 0){
        if((kodas.length >= 8)&&(kodas.length <= 20)){
          if((password.length >= 8)&&(password.length <= 20)){
            EmployeesReg = firebase.database().ref("User/" + key);
            EmployeesReg.update({
              userid: kodas,
              password: sha256(c.a + Data.id + password + c.b)
            });
            alert("Vartotojas sekmingai priregistruotas");
            ReactDOM.render(<Start />, document.getElementById('root'));
          }
          else notification("Slaptažodį turi sudaryti bent 8 simboliai");
        }
        else notification("Vartotojo vardą turi sudaryti bent 8 simboliai");
      }
      else notification("Toks vartotojo vardas jau yra");
    })  
  );  
};

function RegisterFinish(props) {
  var t;
  var nt;
  var rt;
  var usern = /^[0-9a-zA-Z-_.]+$/;
  var passw = /^[0-9a-zA-Z]+$/;
  const [UN, setUN] = useState('');
  nt = "";
  for(var i = 0; i < UN.length; i++){
    t = UN.charAt(i);
    if(t.match(usern)){
      nt = nt + t;
    }
  }
  const [PW, setPW] = useState('');
  rt = "";
  for(var j = 0; j < PW.length; j++){
    t = PW.charAt(j);
    if(t.match(passw)){
      rt = rt + t;
    }
  }
  return (
    <div className="container page-login">
      <div id="login-error"/>
      <img src={logo} className="logo" alt="Hegelmann logo"/>
      <div className="small-container">
        <div id="user" className="content">
          <h2>Registracija</h2>
          <div>
            <input id="code" placeholder="Prisijungimo vardas" className="input-box" type="text" value={nt} onChange={event => setUN(event.target.value)} maxLength="20" />
          </div>
          <div>
            <input id="password" placeholder="Slaptažodis" className="input-box pw" type="password" value={rt} onChange={event => setPW(event.target.value)} maxLength="20" />
          </div>
          <div className="buttons">
            <button id="autobutton" className="menubutton" onClick={() => RegCheck(props.id)}>Registruotis</button>
            <button className="menubutton" onClick={() => window.location.reload(false)}>Grįžti atgal</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default RegisterFinish;

function notification(msg){
  return( 
      document.getElementById("login-error").innerHTML = msg,
      document.getElementById("login-error").style.backgroundColor = "#ff3333",
      document.getElementById("login-error").style.color = "#ffffff",
      setTimeout(() => {
          document.getElementById("login-error").innerHTML = "";
          document.getElementById("login-error").style.backgroundColor = "#ffffff00";
          document.getElementById("login-error").style.color = "#ffffff00";
      }, 2000)
  );    
};